import vue from "vue";
import axios from "axios";
import vm from "../main"
import store from '../plugins/vuex'
//添加一个请求的拦截
axios.interceptors.request.use(function (config) {
    // 设置通用的请求头
    // 设置一些通用的基础连接 baseUrl
    // 强制设置一些请求的姿势
    // config==配置     axios({配置})
    // config.headers={'X-Requested-With': 'XMLHttpRequest'},

    //修改bloading=true
    //this.$root.bbload=true;
    // vm.bbload=true;
    store.commit('VIEW_LOADING',true)
  
    return config;
  }, function (error) {
    // Do something with request error
    return Promise.reject(error);
  });
 
// 添加一个响应的拦截
axios.interceptors.response.use(function (response) {
    // Do something with response data
    // this.$root.bbload=false;
    // vm.bbload=false;
    store.commit('VIEW_LOADING',false)
    return response;
  }, function (error) {
    // Do something with response error
    return Promise.reject(error);
  });
vue.prototype.axios=axios;//实例属性
window.axios=axios;// 全局中API
// axios.shuxing="hjh";  axios 是函数，多次引入的话不是一个同一个函数，函数每执行一次的话，就是复制一份，
// 如果是类的话，多次引入的是同一个类