import Vue from 'vue'
import App from './App.vue'
// main.js 用来引入所有的配置
Vue.config.productionTip = false;
import router from "./plugins/router.js"
import "./assets/js/font.js";
import "./assets/css/base.css"//全局暴露


//配置服务器端的地址
import server from "./config/server"
Vue.prototype.baseUrl=server.baseUrl;
 
// 引入一些全局的过滤器
//过滤器的注册的问题
import filters from "./filter"
for(var key in filters){
  Vue.filter(key,filters[key]);
};

//配置axios
import "./plugins/axios";
import "animate.css"

//引入vuex的配置
import store from "./plugins/vuex"
//同步状态管理和localstorage
//开始的时候设置localstorag中的用户的信息，当没有登录的时候，
//强刷不会进到用户界面，在用户界面的时候，localstorage 有信息，还是在用户界面
let local=window.localStorage.getItem("wmy_user");
if(local){
  store.commit("UPDATE_USER",JSON.parse(local))
}
 let vm=new Vue({
  data:{
  
  },
  router:router,
  store,
  render: h => h(App),
}).$mount('#app')
export default vm;