
export default {
    // 就是将数据进行处理，如果不需要处理就不要放在getters
    //主要就是取数据，将state中的数据整理出来，传给component
   
    column:(state)=>{
        // let err=state.column.err==0 ? true : false;
        // return {err:err,data:state.column.data}
        //状态管理的数据是同步的
    
        return state.column.data ? state.column.data.data :[];
        //column首先要渲染，在返回 我们可以先设置一个空数组表示 ，如果有数据就返回，没有数据就返回一个空数组
    },
    user:(state)=>{
       
        return state.user.data 
        
   
    }
}