<template>
    <div class="newsList">
      <div>
         <span class="shopcar">购物车</span>
            <span v-if="shopcar.length<=0">购物车内无商商品，请选购商品</span>
          <router-link  to="/column" tag="span" class="shop"> 
          选购商品     
          </router-link> 
         
      </div>
     
        <ul>
         <li  v-for="(items,index) of shopcar"
          :key="items._id"
    >
       
       <h2>{{index+1}}/{{items.time|data}}</h2>
        <p>{{items.title}}</p>
         <img :src="baseUrl+items.detail.icon" v-html="baseUrl+items.detail.icon"  alt />
       <h3>单价：$<span v-html="items.price"></span></h3>
       <h4 >数量：{{items.number}}</h4>
    <a @click="changeItem({_id:items._id,num:1})">增加数量</a>
    <a  class="you" @click="changeItem({_id:items._id,num:-1})">减少数量</a>
     <h2>价格：$ {{items.number*items.price}}</h2>
    <a  href="javascript:;" @click="removeItem({_id:items._id})">删除商品</a>
    </li>
    <!-- <h2 v-if="shopcar.length>0" @click="allprice"> 购物车全部价格：$<span>{{$store.state.aprice.date}}</span></h2> -->
     <h2 v-if="shopcar.length>0" > 购物车全部价格：$<span>{{ allprice}}</span></h2>
    <button @click="clearItem" v-show="shopcar.length>0">清空购物车</button>
 </ul>
    </div>
   
</template>

<script>
import {mapState,mapActions} from "vuex"
import store from "../plugins/vuex"
export default {
  // data(){
  // return {
  //   msg:"hoania"
  // }
  // },
      beforeRouteEnter(to,from,next){
   
    // 通过的条件
    console.log(store.state.user)//实例中state中的user 对象
    store.state.user.err===0 ?next():next("/login")
    
  },
    computed:{...mapState(["shopcar"]),
    //计算属性，检测最后的总价
        allprice(){
         let allnum=0;
       store.state.shopcar.forEach((item,index)=>{
      
        allnum+=item.number*item.price;
      });
      return allnum
    }
    },
    
    // methods:mapActions(["changeItem","removeItem","clearItem","allprice"])
    methods:mapActions(["changeItem","removeItem","clearItem",])
}
</script>>
<style scoped>
 .newsList{width:6.4rem; margin:0 auto;}
 .shopcar{display:block;width:1.46rem;height:0.6rem; line-height:0.6rem; text-align:center; font-size:0.3rem; color:#9e9a95;}
 .shop{display:block;line-height:0.6rem;height:0.6rem;font-size:0.3rem; color:rebeccapurple;}
.newsList ul{ padding:0 0.4rem;}
.newsList ul li{ color:#494d4d; padding:0.2rem 0; border-bottom:1px dashed #ccc;}
.newsList ul li img{ display:block;width:100%}
 .newsList ul li a{ max-height:0.9rem; font-size:0.2rem; overflow:hidden;}
  .newsList ul li a.you{float:right}
/* .newsList ul li p{max-height:1.8rem; margin-top:0.05rem;overflow:hidden;}  */
.newsList{clear: both;margin-bottom:1rem;}
button{
    background-color:red;
}
</style>