<template>
  <div id="app">
    <!-- <loading v-if="$root.bbload"></loading> -->
    <loading v-if="$store.state.bbload"></loading>
   <Header v-if="bnav"></Header>
   <!-- <Home></Home> --> 
   <!-- <Follow></Follow> -->
   <!-- <Column></Column> -->
   <!-- <Login></Login> -->
   <!-- <Reg></Reg> -->
   <!-- <Detail></Detail> -->

   <!-- 动画的效果 -->
   <!-- <transition
   enter-active-class="animated bounceInLeft"
   leave-active-class="animated bounceInLeft"
   > 
   <keep-alive>
   <router-view style="position:absolute"></router-view>
    </keep-alive>
    </transition> -->
   
   <keep-alive>
    <router-view ></router-view>
    </keep-alive>

   <!-- <User></User> -->
   <Footer v-if="bfoot"></Footer>
  </div>
</template>

<script>
import Loading from "./components/Loading"
import Detail from "./pages/detail"
import Reg from "./pages/reg"
 import Login from "./pages/login"
// import Follow from "./pages/follow"
import Header  from "./components/Header"
import Footer  from "./components/Footer"


import {mapState} from "vuex"
import * as types from "./store/type"
export default {
  name: 'app',
  components: {
  Header,Login,Footer,Reg,Detail,Loading
  },
  watch:{
    //属性观测，通过$route.path,找到对应的页面是user,还是follow等等
    // 我们主要检测的是真个APP的这个path的属性，因为整个APP不会被撤销
   $route:{
     handler(nextRouter,prevRouter){
      //  console.log(nextRouter.path)
    let path=nextRouter.path;
    //正则匹配
    if(/home|follow|column/.test(path)){
      //  this.$root.bnav=true;
      //  this.$root.bfoot=true;
       this.$store.commit("VIEW_NAV",true);
       this.$store.commit("VIEW_FOOT",true);
    }
    if(/user|shopcar/.test(path)){
      //  this.$root.bnav=false;
      //  this.$root.bfoot=true;
       this.$store.commit("VIEW_NAV",false);
       this.$store.commit("VIEW_FOOT",true);
    }
   
   if(/login|reg|datail/.test(path)){
      //  this.$root.bnav=false;
      //  this.$root.bfoot=false;
      this.$store.commit("VIEW_NAV",false);
      this.$store.commit("VIEW_FOOT",false);
    }
    },
     //首次运行检测
     immediate:true,
   }  
  },
  computed:mapState([
    "bnav","bfoot"
  ])
}
</script>

